<!-- @format -->
<template>
    <v-card class="mx-auto navigation" height="300" width="330">
        <v-navigation-drawer dark permanent width="100%">
            <v-row class="fill-height" no-gutters>
                <v-navigation-drawer dark mini-variant mini-variant-width="56" permanent>
                    <v-list-item class="px-2 logo">
                        <v-list-item-avatar>
                            <v-img
                                src="https://res.cloudinary.com/shuzchef/image/upload/v1622816435/bb5h6tgdysfys9qi1du5.png"
                            ></v-img>
                        </v-list-item-avatar>
                    </v-list-item>

                    <v-divider></v-divider>

                    <v-list dense nav>
                        <v-list-item
                            v-for="item in mainItems"
                            :key="item.title"
                            @click="updateMain(item)"
                        >
                            <v-list-item-action>
                                <v-icon>{{ item.icon }}</v-icon>
                            </v-list-item-action>

                            <v-list-item-content>
                                <v-list-item-title>{{ item.title }}</v-list-item-title>
                            </v-list-item-content>
                        </v-list-item>
                    </v-list>
                </v-navigation-drawer>

                <v-list v-if="selectedMain.title === 'Videos'" class="video-nav inner-list ">
                    <h2>Videos</h2>
                    <game-select-menu-item initialOpen @game-selected="updateGame($event)" />
                    <player-select-menu-item @player-selected="updatePlayer($event)" />
                </v-list>
            </v-row>
        </v-navigation-drawer>
    </v-card>
</template>

<script>
import GameSelectMenuItem from '@/components/games/game-select-menu-item';
import PlayerSelectMenuItem from '@/components/players/player-select-menu-item';

export default {
    components: {
        'game-select-menu-item': GameSelectMenuItem,
        'player-select-menu-item': PlayerSelectMenuItem
    },

    data() {
        return {
            mainItems: [
                { title: 'Videos', icon: 'mdi-video' },
                { title: 'Combos', icon: 'mdi-mixed-martial-arts' },
                { title: 'Matches', icon: 'mdi-kabaddi' },
                { title: 'Games', icon: 'mdi-gamepad-square' },
                { title: 'Favorites', icon: 'mdi-heart' },
                { title: 'Add', icon: 'mdi-plus' },
                { title: 'Collections', icon: 'mdi-book-variant-multiple' }
            ],
            mini: true,
            selectedMain: null,
            selectedGame: null
        };
    },

    created() {
        this.selectedMain = this.mainItems[0];
    },

    methods: {
        updateMain(item) {
            this.selectedMain = item;
        },

        updateGame(game) {
            var searchQuery = {
                type: 'Game',
                value: game.id
            };
            this.setSearch(searchQuery);
        },

        updatePlayer(player) {
            var searchQuery = {
                type: 'Player',
                value: player.id
            };
            this.setSearch(searchQuery);
        },

        setSearch(searchQuery) {
            var query = {
                name: searchQuery.type,
                value: searchQuery.value
            };

            this.$router.push(`/${query.name}/${query.value}`);
        }
    }
};
</script>

<style>
.navigation {
    height: 100vh !important;
}

.navigation .v-navigation-drawer {
    border-radius: 0 !important;
    background: #444;
}

.navigation .logo.v-list-item {
    padding: 0 5px;
}

.navigation .menu-item {
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: #3eb489;
    margin-bottom: 20px;
    line-height: 1.2em;
    font-weight: 300;
    text-decoration: none;
    display: flex;
    align-items: center;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.navigation .inner-list {
    padding: 30px 20px;
    width: 270px;
}
</style>
