<!-- @format -->
<template>
    <div :class="[{ opened: isOpen }, 'player-selct-menu-item']">
        <div class="menu-item" @click="open">
            Players
            <v-icon>
                mdi-chevron-down
            </v-icon>
        </div>

        <div v-if="isOpen" class="players">
            <v-autocomplete
                v-model="selectedItem"
                :items="players"
                label="Players"
                item-text="name"
                item-value="id"
                return-object
            >
            </v-autocomplete>
        </div>
    </div>
</template>

<script>
import PlayersService from '@/services/players-service';

export default {
    name: 'player-select',
    props: {
        initialOpen: {
            type: Boolean,
            default: false
        }
    },

    data() {
        return {
            players: [],
            isOpen: false,
            selectedItem: null,
            isLoading: false
        };
    },

    computed: {
        componentStyle() {
            return '[{opened: }]';
        }
    },

    mounted() {
        this.getPlayers();
        this.isOpen = this.initialOpen;
    },

    methods: {
        async getPlayers() {
            this.isLoading = true;
            const response = await PlayersService.fetchPlayers();
            this.players = response.data.players.map(player => {
                return {
                    id: player._id,
                    name: player.Name,
                    selected: false
                };
            });
            this.isLoading = false;
        },

        selectPlayer(player) {
            this.players.forEach(player => {
                player.selected = false;
            });
            player.selected = true;
            this.$emit('player-selected', player);
        },

        open() {
            this.isOpen = !this.isOpen;
        }
    }
};
</script>
<style type="text/css">
.player-selct-menu-item .logo-img {
    width: 50px;
    height: auto;
    border-radius: 50%;
}

.player-selct-menu-item li {
    list-style: none;
    font-weight: 400;
    cursor: pointer;
    padding: 10px 10px;
}

.player-selct-menu-item li:hover,
.player-selct-menu-item li.selected {
    background: #565656;
}

.player-selct-menu-item li:last-child {
    margin-bottom: 0px;
}

.player-selct-menu-item .players {
    background: #404040;
}

.player-selct-menu-item .mdi-chevron-down {
    transform: rotate(0deg);
    transition: all 0.3s linear;
}

.player-selct-menu-item.opened .mdi-chevron-down {
    transform: rotate(180deg);
    transition: all 0.3s linear;
}
</style>
